* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  body {
    background-color: orange;
    font-family: sans-serif;
    background: #eee;
  }
  
  header {

    display: flex;
    justify-content: center;
    align: center;
    padding: 15px;
    background: #3c3c3c;
    border-bottom: 3px solid #56e388;
  }
  
  header h1 {
    
    color: #eee;
    font-size: 56px;
    font-weight: 700;
    text-transform: uppercase;
  }
  
  .faqs {
    background-color: orange;
    width: 100%;
    max-width: 768px;
    margin: 0 auto;
    padding: 15px;
  }
  
  .faqs .faq {
    background: white;

    margin: 15px;
    padding: 15px;
    border-radius: 10px;
    box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.1);
  }
  
  .faqs .faq .faq-question {
    position: relative;
    font-size: 24px;
    padding-right: 80px;
    transition: all 0.4s ease;
  }
  
  .faqs .faq .faq-question::after {
    content: "+";
    position: absolute;
    top: 50%;
    right: 0px;
    transform: translateY(-50%);
    width: 30px;
    height: 30px;
    transition: all 0.2s ease;
  }
  
  .faqs .faq .faq-answer {
    opacity: 0;
    max-height: 0;
    overflow: hidden;
    transition: all 0.2s ease;
  }
  
  .faqs .faq.open .faq-question {
    margin-bottom: 15px;
  }
  
  .faqs .faq.open .faq-question::after {
    content: "-";
  }
  
  .faqs .faq.open .faq-answer {
    max-height: 1000px;
    opacity: 1;
  }


*{
    box-sizing: border-box;
  }
  
  .navbar {
    background: #333;
    overflow: hidden;
  }
  
  
  .navbar a {
    text-align: center;
    text-decoration: none;
    padding: 17px;
    float:left;
    color: #333;
    font-size: 20px;
  }
  #logo{
    border-right: 10px solid transparent;

    object-position: right 24em;
    width: 540px;
	height: 540px;
	color: linear-gradient(to bottom,  #000000,  #FF99CC);
    text-align: center;
  }
  #menu{
    width: 440px;
	height: 440px;
	color: linear-gradient(to bottom,  white,  #FF99CC);
    object-position: right 24em;
  }
  h3{
    color: white;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-size: 48px;
    text-align: center;
    position: absolute;
  }
  
  .navbar a:hover {
    background: #818181;
  }
  
  .navbar a.active {
    background: #04aa6d;
  }
  
  .dropdown {
    overflow: hidden;
    float: left;
    position: relative;
  }
  
  .dropbtn {
    background: #333;
    color: #fff;
    font-size: 20px;
    border: none;
    padding: 17px;
    margin: 0;
    z-index: 1;
  }
  
  .dropdown-content {
    display: none;
    position: relative;
    background: #f1f1f1;
    min-width: 160px;
    box-shadow: 0 8px 10px rgba(0, 0, 0, 0.2);
  }
  
  .dropdown-content a {
    color: #000;
    display: block;
    float: none;
    text-align: left;
  }
  
  .dropdown-content a:hover {
    color: #fff;
  }
  
  .dropdown:hover .dropbtn {background: #818181;}
  
  .dropdown:hover .dropdown-content {display: block}
